
import React, { Component } from "react";
import Slider from "react-slick";


export default class MultipleItems extends Component {
  render() {
    const settings = {
      className:"multiSlider",
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 2000,

      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 1,
            infinite: true,
            dots: false
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return (
      <div className="multiSlider">
        <Slider {...settings}>
          <div>
            <div className="automation">
              <img className="auto" alt="" src="./Teraone/Vector.svg" />
              
              <h6>Building<br />Automation</h6>
              <img className="arrow1" alt="" src="./Teraone/Arrow 7.png" />
              <p>Our Building Automation services ensure better integration for a comfortable and energy-efficient environment.</p>

            </div>
          </div>
          <div>
            <div className="automation">
              <img className="auto" alt="" src="./Teraone/8324246_ui_essential_app_energy_bolt_icon 1 (1).png" />
              
              <h6>Energy
                <br />Efficiency</h6>

              <img className="arrow1" alt="" src="./Teraone/Arrow 7.png" />
             
              <p>Our expert engineers achieve up to 25% electrical consumption reduction, ensuring a compelling ROI for your business.</p>

            </div>
          </div>
          <div>
            <div className="automation">
              <img className="auto" alt="" src="./Teraone/3709727_problem_process_solution_solve_trouble_icon 1.png" />
             
              <h6>Process <br />Automation</h6>

              <img className="arrow1" alt="" src="./Teraone/Arrow 7.png" />
            
              <p>Boost productivity with custom Process Automation. Achieve 20% performance improvement for your unique systems.</p>

            </div>
          </div>
          <div>
            <div className="automation">
              <img className="auto" alt="" src="./Teraone/g2737.png" />
            
              <h6>Network<br />Design</h6>

              <img className="arrow1" alt="" src="./Teraone/Arrow 7.png" />
             
              <p>Seamless automation with advanced network designs. Fast, reliable connections tailored to your policies.</p>

            </div>
          </div>
          <div>
            <div className="automation">
              <img className="auto" alt="" src="./Teraone/g2737.png" />
             
              <h6>Network<br />Design</h6>

              <img className="arrow1" alt="" src="./Teraone/Arrow 7.png" />
              <p>Seamless automation with advanced network designs. Fast, reliable connections tailored to your policies.</p>

            </div>
          </div>



        </Slider>
      </div>
    );
  }
}